@import url(https://fonts.googleapis.com/css?family=Karla:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: "Karla", sans-serif, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black; }

.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

button {
  background-color: white; }

main {
  display: flex;
  justify-content: center;
  align-content: center;
  overflow: hidden;
  flex: 1 0; }

.grower {
  flex-grow: 5;
  min-height: 5px; }

.fix-top {
  flex: 0 0 60px; }

.fix-bottom {
  flex: 0 0 60px; }

footer {
  background-color: white; }

.nav-style {
  color: black;
  background-color: white;
  z-index: 3;
  margin: 0;
  padding: 0;
  height: 60px;
  display: flex;
  align-content: center;
  justify-content: center;
  width: 100vw;
  border-radius: 0px; }
  .nav-style ul {
    display: flex; }
  .nav-style li {
    display: inline;
    text-decoration: none;
    align-self: center;
    justify-self: center; }
  .nav-style a {
    color: pink; }

.nav-link {
  align-self: center;
  justify-self: center;
  text-align: center;
  font-family: "Karla", sans-serif;
  font-size: 1rem;
  font-weight: 400; }

.nav-width {
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
  flex-direction: row;
  justify-content: space-between; }

#top-nav {
  position: fixed;
  top: 0;
  padding: 0 30px; }

section {
  width: 90vw;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center; }

.landing-wrapper {
  display: flex;
  flex-direction: row;
  align-self: center;
  justify-self: center;
  padding: 20px 0; }

.landing-text {
  font-family: "Karla", sans-serif;
  text-align: center;
  font-size: 3.5rem;
  color: blue; }

.landing-random {
  display: inline; }

.action {
  font-weight: 400; }

.object {
  font-weight: 700; }

.lang-button {
  align-self: center;
  background-color: blue;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  text-align: center;
  font-family: "Karla", sans-serif;
  font-size: 10px;
  font-weight: 700; }

@media only screen and (max-width: 800px) {
  .landing-text {
    font-size: 2rem; } }


